import React from "react";
import classNames from "classnames";
import { SectionProps } from "../../utils/SectionProps";
import SectionHeader from "./partials/SectionHeader";
import Timeline from "../elements/Timeline";
import TimelineItem from "../elements/TimelineItem";

const propTypes = {
  ...SectionProps.types,
};

const defaultProps = {
  ...SectionProps.defaults,
};

class Roadmap extends React.Component {
  render() {
    const {
      className,
      topOuterDivider,
      bottomOuterDivider,
      topDivider,
      bottomDivider,
      hasBgColor,
      invertColor,
      ...props
    } = this.props;

    const outerClasses = classNames(
      "roadmap section",
      topOuterDivider && "has-top-divider",
      bottomOuterDivider && "has-bottom-divider",
      hasBgColor && "has-bg-color",
      invertColor && "invert-color",
      className
    );

    const innerClasses = classNames(
      "roadmap-inner section-inner",
      topDivider && "has-top-divider",
      bottomDivider && "has-bottom-divider"
    );

    const sectionHeader = {
      title: "Our Process",
      paragraph:
        "We're startup people too, your success is our success. We'll repeat this process as many times as you need.",
    };

    return (
      <section {...props} className={outerClasses} id="timeline">
        <div className="container">
          <div className={innerClasses}>
            <SectionHeader data={sectionHeader} className="center-content" />
            <Timeline>
              <TimelineItem title="Discovery call">
                First, we talk with you about your startup, your USP, your
                brand, and your customers to identify your needs.
              </TimelineItem>
              <TimelineItem title="Iterative design">
                Then, our design team shares every step of their work on layout
                and copy with you for comment and revision.
              </TimelineItem>
              <TimelineItem title="Brand identity">
                We create bespoke product illustrations and brand-consistent UI
                components for your new site.
              </TimelineItem>
              <TimelineItem title="Optimization">
                Once you're ready to go live, we'll perfect your SEO and
                performance.
              </TimelineItem>
              <TimelineItem title="Site management">
                You can manage and update your website's content through our
                dashboard.
              </TimelineItem>
              <TimelineItem title="Iterate and revise">
                And at any time, you can request free, unlimited revisions and
                advice from our team of experts
              </TimelineItem>
            </Timeline>
          </div>
        </div>
      </section>
    );
  }
}

Roadmap.propTypes = propTypes;
Roadmap.defaultProps = defaultProps;

export default Roadmap;
