import React from "react";
import classNames from "classnames";
import { SectionSplitProps } from "../../utils/SectionProps";
import ButtonGroup from "../elements/ButtonGroup";
import Button from "../elements/Button";
import Image from "../elements/Image";
import Modal from "../elements/Modal";
import Marquee from "react-marquee-slider";
import HeroCarousel from "../sections/HeroCarousel";
import PhoneMockup from "../elements/PhoneMockup";
import AnchorLink from "react-anchor-link-smooth-scroll/src";

const propTypes = {
  ...SectionSplitProps.types,
};

const defaultProps = {
  ...SectionSplitProps.defaults,
};

class HeroSplit extends React.Component {
  state = {
    videoModalActive: false,
  };

  openVideoModal = (e) => {
    e.preventDefault();
    this.setState({ videoModalActive: true });
  };

  closeVideoModal = (e) => {
    e.preventDefault();
    this.setState({ videoModalActive: false });
  };

  render() {
    const {
      className,
      topOuterDivider,
      bottomOuterDivider,
      topDivider,
      bottomDivider,
      hasBgColor,
      invertColor,
      invertMobile,
      invertDesktop,
      alignTop,
      imageFill,
      ...props
    } = this.props;

    const outerClasses = classNames(
      "hero section",
      topOuterDivider && "has-top-divider",
      bottomOuterDivider && "has-bottom-divider",
      hasBgColor && "has-bg-color",
      invertColor && "invert-color",
      className
    );

    const innerClasses = classNames(
      "hero-inner section-inner",
      topDivider && "has-top-divider",
      bottomDivider && "has-bottom-divider"
    );

    const splitClasses = classNames(
      "split-wrap",
      invertMobile && "invert-mobile",
      invertDesktop && "invert-desktop",
      alignTop && "align-top"
    );

    return (
      <>
        <section {...props} className={outerClasses}>
          <div className="container">
            <div className={innerClasses}>
              <div className={splitClasses}>
                <div className="split-item">
                  <div className="hero-content split-item-content center-content-mobile reveal-from-top">
                    <h1 className="mt-0 mb-4">
                      A highly-converting website for your startup
                    </h1>
                    <p className="mt-0 mb-8">
                      PageKnight is your always available website team. We're
                      experts in copywriting, SEO, illustration, and design. We
                      do the heavy-lifting so you can focus on building your
                      business.
                    </p>
                    <ButtonGroup>
                      <Button
                        tag={AnchorLink}
                        color="primary"
                        href="#contact"
                        wideMobile
                      >
                        Book a call
                      </Button>
                      <Button
                        tag="a"
                        color="dark"
                        href="#timeline"
                        wideMobile
                        tag={AnchorLink}
                      >
                        Learn more
                      </Button>
                    </ButtonGroup>
                  </div>
                  <div className="hero-figure split-item-image split-item-image-fill illustration-element-01 reveal-from-bottom mt-32 md:mt-0 hidden sm:block">
                    <PhoneMockup>
                      <img
                        src={
                          "https://assets.revolut.com/media/index/Home-Analytics.png"
                        }
                      />
                    </PhoneMockup>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        <HeroCarousel />
      </>
    );
  }
}

HeroSplit.propTypes = propTypes;
HeroSplit.defaultProps = defaultProps;

export default HeroSplit;
