import React, {useState} from "react"
import Marquee from "react-marquee-slider";
import LandingPage1 from '../../assets/images/landing-page-1.png'
import LandingPage2 from '../../assets/images/landing-page-2.png'
import LandingPage3 from '../../assets/images/landing-page-3.png'
import LandingPage4 from '../../assets/images/landing-page-4.png'
import LandingPage5 from '../../assets/images/landing-page-5.png'
import LandingPage6 from '../../assets/images/landing-page-6.jpeg'
import LandingPage7 from '../../assets/images/landing-page-7.png'


const sources = [
    LandingPage1,
    LandingPage2,
    LandingPage3,
    LandingPage4,
    LandingPage5,
    LandingPage6,
    LandingPage7
]

const HeroCarousel = (props) => {

    const [loaded, setLoaded] = useState(sources.map(() => false))

    const renderItem = (source, i) =>
        <div className="flex flex-col justify-center shadow-lg overflow-visible mx-4 mb-8">
            <img className="mx-auto" src={source} width="432" height="243" alt="Hero" onLoad={() => {
                if (!loaded[i]) {
                    setLoaded(loaded.map((v, j) => i === j ? true : v))
                }
            }}/>
        </div>


    return (
        <>
            {loaded.filter((v) => !v).length === 0 ? <div className={"transform rotate-landing sm:-translate-y-3"}>
                <Marquee direction={"rtl"} velocity={10} scatterRandomly={false}>
                    {sources.map(renderItem)}
                </Marquee>
            </div> : <div className="absolute hidden">{sources.map(renderItem)}</div>

            }
        </>

    )
}

export default HeroCarousel